<script lang="coffeescript">
  import { tick, createEventDispatcher } from 'svelte'
  import { create, all } from 'mathjs'
  math = create(all, { number: 'Fraction'})
  dispatch = createEventDispatcher()

  `export let item`
  `export let index`
  import InlineInput from "../../../components/common/InlineInput.svelte"
  import IconButton from "../../../components/common/IconButton.svelte"

  expressionChange = (e)->
    dispatch "changeExpression", {id: item.id, expression: e.detail }
    item.expression = e.detail


  deleteLine = (e)->
    dispatch "delete", item.id




  copy = (e)->
    dummy = document.createElement("input")
    # dummy.style.display = 'none'
    document.body.appendChild(dummy)

    dummy.setAttribute("id", "clipboard-#{item.id}")
    document.getElementById("clipboard-#{item.id}").value=item.value
    console.log document.getElementById("clipboard-#{item.id}").value
    dummy.select()
    document.execCommand("copy")
    document.body.removeChild(dummy)
    console.log "copied #{item.value}"




  evaluateExpression = (expression)->
    invalidExpression = ""

    if expression?
      if expression.trim().length > 0
        try
          return math.evaluate(expression)
        catch e
          #console.log e

          try
            dateattempt = Date.create(expression)
            if dateattempt.isValid()
              return dateattempt
            else
              return invalidExpression
          catch e2
            #console.log e2
            return invalidExpression
      else
        return ""
    else
      return ""


</script>
<style lang="sass">

  section
    div:first-child
      padding-left: 0px
      padding-right: 8px
    div
      padding-left: 8px
    div:last-child
      text-align: right
      padding: 0px
      min-width: 80px


</style>



<section layout="row">
  <div flex="40">
    <InlineInput value={item.expression} on:update={expressionChange}/>
  </div>
  <div flex>
    {evaluateExpression(item.expression)}
  </div>
  <div flex="initial">
    <IconButton on:clicked={deleteLine}>
      <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12 19 6.41z"/></svg>
    </IconButton>
    <IconButton on:clicked={copy}>
      <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M16 1H4c-1.1 0-2 .9-2 2v14h2V3h12V1zm-1 4H8c-1.1 0-1.99.9-1.99 2L6 21c0 1.1.89 2 1.99 2H19c1.1 0 2-.9 2-2V11l-6-6zM8 21V7h6v5h5v9H8z"/></svg>
    </IconButton>
  </div>

</section>
