<script lang="coffeescript">
  import { tick, createEventDispatcher } from 'svelte'

  dispatch = createEventDispatcher()

  `export let line`
  import InlineInput from "../../../components/common/InlineInput.svelte"

  keyChange = (e)->
    dispatch "changeKey", {id: line.id, key: e.detail }
    line.key = e.detail

  valueChange = (e)->
    dispatch "changeValue", {id: line.id, value: e.detail }
    line.value = e.detail

  deleteLine = (e)->
    dispatch "delete", line.id

  cloneLine = (e)->
    dispatch "clone", line.id

  isLink = (text)->
    text = text || ""
    httpLink = text.startsWith "http://"
    httpsLink = text.startsWith "https://"
    fileLink = text.startsWith "file://"
    mailtoLink = text.startsWith "mailto://"
    telLink = text.startsWith "tel://"

    return httpLink || httpsLink || fileLink ||  mailtoLink || telLink


  copy = (e)->
    dummy = document.createElement("input")
    # dummy.style.display = 'none'
    document.body.appendChild(dummy)

    dummy.setAttribute("id", "clipboard-#{line.id}")
    document.getElementById("clipboard-#{line.id}").value=line.value
    console.log document.getElementById("clipboard-#{line.id}").value
    dummy.select()
    document.execCommand("copy")
    document.body.removeChild(dummy)
    console.log "copied #{line.value}"

  toggleVisibility = (e)->
    if line.is_visible?
      line.is_visible = !line.is_visible
    else
      line.is_visible = true
    dispatch "changeVisibility", {id: line.id, is_visible: line.is_visible }

  go = ()->
    console.log "go"
    console.log line.value
    window.open line.value, "_blank"
</script>

<tr>
  <td>
    {line.date}
  </td>
  <td>
      <InlineInput value={line.value} on:update={valueChange}/>
  </td>
  <td>
    <button on:click={deleteLine} type="button">delete</button>
    <button on:click={cloneLine} type="button">clone</button>
  </td>

</tr>
