<script lang="coffeescript">

  `export let item`
  import uuid from 'uuid'
  import { tick, createEventDispatcher, onMount } from 'svelte'
  import {databaseLoaded,  collection} from "../../../helpers/stores.js"
  import KeyPair from "./_KeyPair.svelte"

  import DraggableList from "../../../components/common/DraggableList.svelte"

  mounted = false

  onMount ()->
    mounted = true

  keypairElementId = "new-keypair-#{item.id}"
  newItem = null

  saveCb = (r)->
    console.log "saved!"
    console.log r
    reset(true)

  update = ()->
    reindex()
    collection.save {id: item.id, data: { lines: item.data.lines }}, saveCb


  add = ()->
    console.log "add"
    console.log newItem
    # set ordinal to max + 1
    newItem.created_at = Date.create()
    newItem.modified_at = newItem.created_at
    item.data.lines.push newItem
    update()


  reset = (saved)->
    newItem = {
      id: uuid()
      ordinal: item.data.lines.length
      key: ""
      value: ""
      is_visible: true
      created_at: null
      modified_at: null
    }
    unless saved?
      if document.getElementById(keypairElementId)?
        # document.getElementById(keypairElementId).focus()
        1
  reset()



  changeKey = (e)->
    console.log "changeKey"
    console.log e.detail
    itemToChangeIndex = item.data.lines.findIndex (o)->
      o.id == e.detail.id

    console.log item.data.lines[itemToChangeIndex]

    item.data.lines[itemToChangeIndex].key = e.detail.key
    update()

  changeValue = (e)->
    console.log "changeValue"
    console.log e.detail
    itemToChangeIndex = item.data.lines.findIndex (o)->
      o.id == e.detail.id

    console.log item.data.lines[itemToChangeIndex]
    item.data.lines[itemToChangeIndex].value = e.detail.value
    update()

  changeVisibility = (e)->
    console.log "changeVisibility"
    console.log e.detail
    itemToChangeIndex = item.data.lines.findIndex (o)->
      o.id == e.detail.id

    console.log item.data.lines[itemToChangeIndex]

    item.data.lines[itemToChangeIndex].is_visible = e.detail.is_visible
    update()



  reindex = ()->
    item.data.lines.forEach (item,index)->
      item.ordinal = index

  reorder = (ev)->
    item.data.lines = ev.detail
    update()


  deleteLine = (e)->
    item.data.lines.remove (o)->
      o.id == e.detail
    # todo: reindex ordinals
    update()

  checkKeys = (e)->
    console.log e
    if (e.code == "NumpadEnter") || ((e.code == "Enter") && !e.shiftKey)
      add()

</script>
<style lang="sass">

  header

    margin-bottom: 18px
    div:first-child
      padding-right: 8px
    div:last-child
      padding-left: 8px

  input
    width: 100%

</style>

<header layout="row">
  <div flex="40">
    <input id={keypairElementId} type="text" bind:value={newItem.key} on:keyup={checkKeys} placeholder="Label"/>
  </div>
  <div flex="60">
    <input type="text" bind:value={newItem.value} on:keyup={checkKeys} placeholder="Data"/>
  </div>
</header>




<section>
    {#if mounted}
    <DraggableList
        list={item.data.lines}
        key="id"
        on:sort={reorder}
        let:item
    		let:index
    >
        <KeyPair {item} {index} on:changeKey={changeKey} on:changeValue={changeValue} on:changeVisibility={changeVisibility} on:delete={deleteLine}/>
    </DraggableList>
    {/if}



</section>
