<script lang="coffeescript">

  `export let item`

  import { create, all } from 'mathjs'

  import uuid from 'uuid'

  import { tick, createEventDispatcher, onMount } from 'svelte'
  import {databaseLoaded,  collection} from "../../../helpers/stores.js"
  import Solver from "./_Solver.svelte"
  import DraggableList from "../../../components/common/DraggableList.svelte"

  mounted = false

  onMount ()->
    mounted = true


  math = create(all, { number: 'Fraction'})

  expressionElementId = "new-solver-#{item.id}"


  newItem = null

  saveCb = (r)->
    console.log "saved!"
    console.log r
    reset(true)

  update = ()->
    reindex()
    collection.save {id: item.id, data: { lines: item.data.lines }}, saveCb


  add = ()->
    console.log "add"
    console.log newItem
    # set ordinal to max + 1
    newItem.created_at = Date.create()
    newItem.modified_at = newItem.created_at
    item.data.lines.push newItem
    update()


  reset = (saved) ->
    newItem =
      id: uuid()
      ordinal: item.data.lines.length
      expression: ""
      created_at: null
      modified_at: null
    if saved?
      console.log "saved: #{saved}"
      #el = document.getElementById(expressionElementId)
      #if el?
      #  el.focus()

  reset()



  changeExpression = (e)->

    itemToChangeIndex = item.data.lines.findIndex (o)->
      o.id == e.detail.id

    item.data.lines[itemToChangeIndex].expression = e.detail.expression
    update()


  evaluateExpression = (expression)->
    invalidExpression = ""

    if expression?
      if expression.trim().length > 0
        try
          return math.evaluate(expression)
        catch e
          #console.log e

          try
            dateattempt = Date.create(expression)
            if dateattempt.isValid()
              return dateattempt
            else
              return invalidExpression
          catch e2
            #console.log e2
            return invalidExpression
      else
        return ""
    else
      return ""


  reindex = ()->
    item.data.lines.forEach (item,index)->
      item.ordinal = index

  reorder = (ev)->
    item.data.lines = ev.detail
    update()

  deleteLine = (e)->
    item.data.lines.remove (o)->
      o.id == e.detail
    # todo: reindex ordinals
    update()

  checkKeys = (e)->
    if (e.code == "NumpadEnter") || ((e.code == "Enter") && !e.shiftKey)
      if newItem.expression.length > 0
        add()

</script>
<style lang="sass">
  footer

    div:first-child
      padding-right: 8px
    div:last-child
      padding-left: 8px

  input
    width: 100%

</style>




<section>
    {#if mounted}
    <DraggableList
        list={item.data.lines}
        key="id"
        on:sort={reorder}
        let:item
    		let:index
    >
        <Solver {item} {index} on:changeExpression={changeExpression} on:delete={deleteLine}/>
    </DraggableList>
    {/if}


</section>
<footer layout="row">
  <div flex="40">
    <input id={expressionElementId} type="text" bind:value={newItem.expression} on:keyup={checkKeys}/>
  </div>
  <div flex="60">
    {evaluateExpression(newItem.expression)}
  </div>
</footer>
