<script lang="coffeescript">
  import Sugar from "sugar"
  Sugar.extend()

  `export let relativeDate`



  weeks = []


  currentCalendar = []
  currentMonth = null
  currentCalendarHeader = null
  currentDate = null

  generate = ()->
    weeks = []
    if currentDate?
      currentCalendarHeader = currentDate.format("{Month} {yyyy}")
      currentMonth = currentDate.format("{M}")
      calendarStart = currentDate.clone().beginningOfMonth().beginningOfWeek()
      calendarEnd = currentDate.clone().endOfMonth().endOfWeek()
      runningDate = calendarStart.clone()
      currentWeek = []
      count = 0
      while runningDate.isBetween(calendarStart, calendarEnd)
        currentWeek.push runningDate.clone()
        runningDate.addDays(1)
        count++
        if count == 7
          weeks.push currentWeek
          currentWeek = []
          count = 0







  current = ()->
    currentDate = Date.create().clone()
    console.log currentDate

    generate()

  next = ()->
    currentDate.addMonths(1)
    generate()
    console.log "currentDate #{currentDate}"


  previous = ()->
    currentDate.addMonths(-1)
    generate()
    console.log "currentDate #{currentDate}"

  nextYear = ()->
    currentDate.addYears(1)
    generate()
    console.log "currentDate #{currentDate}"


  previousYear = ()->
    currentDate.addYears(-1)
    generate()
    console.log "currentDate #{currentDate}"

  current()




</script>

<style lang="sass">
  table
    border: 1px solid gray
    padding: 14px

  tr[monthname]
    td
      font-weight: bold
      text-align: center
      padding: 6px 0px 18px 0px
      font-size: 18px
  tr[weekdays]
    td
      text-align: center

  tbody
    td
      text-align: center
      height: 32px
      width: 32px
      font-weight: bold

  [month=previous]
    color: #dddddd
  [month=next]
    color: #dddddd

  tfoot
    button
      padding: 0px
      background: none
      border: none
      &:hover
        color: blue
</style>

<h1>Calendar</h1>







<table calendar>
  <thead>
    <tr monthname>
      <td colspan="7">{currentCalendarHeader}</td>
    </tr>
    <tr weekdays>
      <td>S</td>
      <td>M</td>
      <td>T</td>
      <td>W</td>
      <td>T</td>
      <td>F</td>
      <td>S</td>
    </tr>
  </thead>
  <tbody>
    {#each weeks as week, weekIndex}
      <tr>
        {#each week as day, dayindex}
          {#if (parseInt(day.format("{M}")) != parseInt(currentMonth)) }
            {#if (weekIndex < 1) }
              <td month="previous">{day.format("{d}")}</td>
            {:else}
              <td month="next">{day.format("{d}")}</td>
            {/if}
          {:else}
            <td month="current">{day.format("{d}")}</td>
          {/if}
        {/each}
      </tr>
    {/each}
  </tbody>
  <tfoot>
    <tr>
      <td><button type="button" on:click={previousYear}>&laquo;</button></td>
      <td><button type="button" on:click={previous}>&lt;</button></td>
      <td></td>
      <td><button type="button" on:click={current}>&bull;</button></td>
      <td></td>
      <td><button type="button" on:click={next}>&gt;</button></td>
      <td><button type="button" on:click={nextYear}>&raquo;</button></td>
    </tr>
  </tfoot>

</table>
