<script lang="coffeescript">

  `export let item`
  import uuid from 'uuid'
  import { tick, createEventDispatcher } from 'svelte'
  import {databaseLoaded,  collection} from "../../../helpers/stores.js"
  import Timesheet from "./_Timesheet.svelte"

  console.log "Timesheet!"
  console.log item

  newItem = null

  saveCb = (r)->
    console.log "saved!"
    console.log r
    reset()

  update = ()->
    collection.save {id: item.id, data: { lines: item.data.lines }}, saveCb


  add = ()->
    console.log "add"
    console.log newItem
    # set ordinal to max + 1
    newItem.created_at = Date.create()
    newItem.modified_at = newItem.created_at
    item.data.lines.push newItem
    update()


  reset = ()->
    newItem = {
      id: uuid()
      date: ""
      detail: []
      hours: ""
      memo: ""
      created_at: null
      modified_at: null
    }
  reset()



  changeKey = (e)->
    console.log "changeKey"
    console.log e.detail
    itemToChangeIndex = item.data.lines.findIndex (o)->
      o.id == e.detail.id

    console.log item.data.lines[itemToChangeIndex]

    item.data.lines[itemToChangeIndex].key = e.detail.key
    update()

  changeValue = (e)->
    console.log "changeValue"
    console.log e.detail
    itemToChangeIndex = item.data.lines.findIndex (o)->
      o.id == e.detail.id

    console.log item.data.lines[itemToChangeIndex]
    item.data.lines[itemToChangeIndex].value = e.detail.value
    update()

  changeVisibility = (e)->
    console.log "changeVisibility"
    console.log e.detail
    itemToChangeIndex = item.data.lines.findIndex (o)->
      o.id == e.detail.id

    console.log item.data.lines[itemToChangeIndex]

    item.data.lines[itemToChangeIndex].is_visible = e.detail.is_visible
    update()




  deleteLine = (e)->
    item.data.lines.remove (o)->
      o.id == e.detail
    # todo: reindex ordinals
    update()

  checkKeys = (e)->
    console.log e
    if (e.code == "NumpadEnter") || ((e.code == "Enter") && !e.shiftKey)
      add()


  start_date = Date.create().format("{yyyy}-{MM}-{dd}")
  end_date = Date.create("next week").format("{yyyy}-{MM}-{dd}")
  weekdays_only = true

  generate = ()->
    lines = []

    lineDate = Date.create(start_date).clone()
    while lineDate.isBetween(Date.create(start_date),Date.create(end_date))
      lines.push
        id: uuid()
        date: lineDate.format("{yyyy}-{MM}-{dd}")
        hours: 0
        is_billable: true
        memo: ""
        created_at: Date.create()
        modified_at: Date.create()
      lineDate.advance("1 day")
    item.data.lines = lines
    update()

</script>
<style lang="sass">

  article[root]

    header

      margin-bottom: 18px

    article

      h1
        font-size: 18px



</style>

<div>
  <div>
    <input type="text" bind:value={start_date}>
  </div>
  <div>
    <input type="text" bind:value={end_date}>
  </div>
  <div>
    <input type="checkbox" bind:value={weekdays_only}>
  </div>


  <table>
    <thead>
      <tr>
        <th>Date</th>
        <th>Details</th>
        <th>Total Hours</th>
        <th>&nbsp;</th>
      </tr>
    </thead>
    <tbody>
      {#each item.data.lines as line}
        <Timesheet {line} on:changeKey={changeKey} on:changeValue={changeValue} on:changeVisibility={changeVisibility} on:delete={deleteLine}/>
      {/each}
    </tbody>

  </table>

</div>
